import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './App.css';

function App() {

const [cards, setCards] = useState([]);
const [arrows, setArrows] = useState({});

const searchPokemon= () => {
  console.log("this works")
  axios.get("https://pokeapi.co/api/v2/pokemon")
  .then(response => {
    setCards(response.data.results);
    setArrows(response.data);
  }
  )
}

useEffect(()=>{
  console.log("this works")
  axios.get("https://pokeapi.co/api/v2/pokemon")
  .then(response => {
    setCards(response.data.results);
    setArrows(response.data);
  })
},[setCards] )

const moreLessPokemon= (url) => {
  console.log(url)
  axios.get(url)
  .then(response => {
    setCards(response.data.results);
    setArrows(response.data);
  })}

  return (
    <div className="App">
      <h1 className="pressstart">Pokedex</h1>
      <button onClick={()=>moreLessPokemon(arrows.previous)}>Previous</button>
      <button onClick={searchPokemon}>reset pokemon</button>
      <button onClick={()=>moreLessPokemon(arrows.next)}>next</button>
      <div className="card-list">
      {
        cards.map(card =>{
          const id = card.url.substring(33).replace( /\//g, '')
        return(
          <div className="card-container" key={id}>
            <img alt="pokemon" key={id} src={`https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${id}.png`}/>
          <p className="pressstart">{card.name}</p>
          </div>
        )})
      }
      </div>
    </div>
  );
}

export default App;


